/* Dashboard.css - Dark Mode */
.dashboard {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #1e1e1e; /* Dark header background */
  color: #ffffff; /* Light text */
}

.main-content {
  display: flex;
  flex: 1;
  background-color: #121212; /* Dark background */
}

.friend-list {
  width: 25%;
  background-color: #1e1e1e; /* Dark sidebar */
  border-right: 1px solid #333; /* Darker border */
  padding: 10px;
}

.friend-list h3 {
  margin-top: 0;
  color: #ffffff; /* Light text */
}

.friend-list ul {
  list-style-type: none;
  padding: 0;
}

.friend-item {
  padding: 10px;
  cursor: pointer;
  border-bottom: 1px solid #333; /* Darker border */
  color: #ffffff; /* Light text */
}

.friend-item:hover, .friend-item.active {
  background-color: #007bff;
  color: white;
}

.conversation {
  flex: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #181818; /* Dark conversation area */
  color: #ffffff; /* Light text */
}

.conversation h3 {
  margin-top: 0;
}

.messages {
  flex: 1;
  overflow-y: auto;
  margin-bottom: 10px;
}

.message {
  margin: 5px 0;
}

.message.sent {
  text-align: right;
  color: #4da6ff; /* Light blue text for sent messages */
}

.message.received {
  text-align: left;
  color: #4caf50; /* Light green text for received messages */
}

.message-input {
  display: flex;
}

.message-input input {
  flex: 1;
  padding: 10px;
  border: 1px solid #333; /* Darker border */
  border-radius: 4px;
  margin-right: 10px;
  background-color: #2e2e2e; /* Dark input background */
  color: #ffffff; /* Light text */
}

.message-input button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.sign-out-btn {
  background-color: #dc3545;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

/* Add Friend Section */
.add-friend-section {
  display: flex;
  flex-direction: column;
  background-color: #181818; /* Dark conversation area */
  padding: 20px;
  color: #ffffff; /* Light text */
}

.add-friend-section h3 {
  margin-top: 0;
}

.add-friend-section input {
  padding: 10px;
  border: 1px solid #333; /* Darker border */
  border-radius: 4px;
  background-color: #2e2e2e; /* Dark input background */
  color: #ffffff; /* Light text */
  margin-bottom: 10px;
}

.similar-users-list {
  list-style-type: none;
  padding: 0;
}

.similar-users-list li {
  padding: 10px;
  border-bottom: 1px solid #333; /* Darker border */
  cursor: pointer;
  color: #ffffff; /* Light text */
}

.similar-users-list li:hover {
  background-color: #007bff;
  color: white;
}

.add-friend-btn {
  margin-top: 20px;
  background-color: #28a745;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
